import React, { Component } from 'react';
import { Row, Col, Form, Input, Select, DatePicker } from 'antd';
import FilterPathEducate from 'components/common/input/FilterPathEducate';

const { Option } = Select;
@Form.create({ name: 'FormSearchStudent' })
class FormSearchStudent extends Component {
  state = {};
  handleReset = () => {
    this.props.form.resetFields();
  };
  handleSearch = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      console.log('Received values of form: ', values);
    });
  };
  render() {
    const { getFieldDecorator } = this.props.form;
    return (
      <Form layout="vertical" onSubmit={this.handleSearch}>
        <Row gutter={24}>
          <FilterPathEducate form={this.props.form} />
          <Col span={8}>
            <Form.Item label="Học kỳ">
              {getFieldDecorator('system5', {})(
                <Select>
                  <Option value="1">Học kỳ 1</Option>
                  <Option value="2">Học kì 2</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Năm học">
              {getFieldDecorator('system6', {})(
                <Select>
                  <Option value="1">2017-2018</Option>
                  <Option value="2">2018-2019</Option>
                  <Option value="3">2019-2020</Option>
                  <Option value="4">2020-2021</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Lần thi">
              {getFieldDecorator('class', { initialValue: '' })(
                <Select defaultValue="lucy" style={{ width: 200 }}>
                  <Option value="lucy">01</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Đợt thi">
              {getFieldDecorator('dt', { initialValue: '' })(
                <Select defaultValue="lucy" style={{ width: 200 }}>
                  <Option value="lucy">01</Option>
                </Select>
              )}
            </Form.Item>
          </Col>

          <Col span={8}>
            <Form.Item label="Ngày giờ thi" hasFeedback>
              {getFieldDecorator('birthdate')(<DatePicker showTime />)}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Ca thi">
              {getFieldDecorator('cathi', { initialValue: '' })(
                <Select defaultValue="lucy" style={{ width: 200 }}>
                  <Option value="lucy">01</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="Học phần">
              {getFieldDecorator('hp', {})(
                <Select mode="multiple">
                  <Option value="1">Chủ nghĩa xã hội</Option>
                  <Option value="2">Giáo dục thể chất</Option>
                  <Option value="3">Toán rời rạc</Option>
                </Select>
              )}
            </Form.Item>
          </Col>
          <Col span={8}>
            <Form.Item label="SBD bắt đầu từ">
              {getFieldDecorator('hsbdp', {})(<Input />)}
            </Form.Item>
          </Col>
        </Row>
        <Row />
      </Form>
    );
  }
}

export default FormSearchStudent;



